.AddEventForm {
    min-width: 280px;
}

.date {
    text-align:center;
    font-size: 2.2rem;
    margin-bottom: 2rem;
}

.events {
    margin: 2rem 0;
}

.events .noEvents {
    color: #999;
}

.eventList {
    list-style:none;
    padding:0 0 0 0.5rem;
    margin: 0;
    font-weight:300;
}

.eventList li {
    margin-top: 0.5rem;
    padding-left: 0.5rem;
    border-left: 1px solid #4A90E2;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow-x: hidden;
}

.buttonRow {
    margin: 0 0 1rem 0;
    text-align: right;
}

@media screen and (min-width:1000px) {
    .AddEventForm {
        max-width: 900px;
    }
}


/* General form styling, would be moved elsewhere if more than one form existed */
input {
    margin: 0 0 1rem 0;
    width: 100%;
    border: 1px solid #ccc;
    font-size:1.6rem;
    padding: 0.5rem 1rem;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}

input:focus {
    border: 1px solid #4A90E2;
}

button {
    background: none;
    border: none;
    color: #000;
    font-weight:700;
    cursor: pointer;
    font-size: 1.6rem;
    padding: 0;
    -webkit-transition: color 250ms;
    -o-transition: color 250ms;
    transition: color 250ms;
}

button:hover {
    color: #4A90E2;
}



